'use strict';

var Css = require("bs-css/src/Css.js");
var React = require("react");

function TextCard(Props) {
  var children = Props.children;
  return React.createElement("div", {
              className: "col-md-12 " + Css.style(/* :: */[
                    Css.margin2(Css.px(12), Css.px(0)),
                    /* :: */[
                      Css.padding2(Css.px(20), Css.px(20)),
                      /* :: */[
                        Css.boxShadow(Css.Shadow.box(undefined, Css.px(1), Css.px(2), undefined, undefined, Css.rgb(200, 200, 200))),
                        /* :: */[
                          Css.borderRadius(Css.px(3)),
                          /* :: */[
                            Css.background(Css.hex("f9f9f9")),
                            /* [] */0
                          ]
                        ]
                      ]
                    ]
                  ])
            }, children);
}

var make = TextCard;

exports.make = make;
